html,
body,
#root {
  height: 100%;
  margin: 0px;
  padding: 0px;
}

.App {
  text-align: center;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* DrawingLists, ul, DrawingList */

.DrawingLists ul {
  list-style-type: none;
  margin: 0;
  padding: 0em 2em;
}
.DrawingLists ul .DrawingList {
  background: #fdfdfd;
  padding: 0.5em 0;
  border-bottom: 1px solid #e2e2e2;
  cursor: pointer;
  transition-duration: 300ms;
  font-size: 1.2em;
  font-weight: 600;
}
.DrawingLists ul .DrawingList:hover {
  background: rgb(236, 236, 236);
}
.DrawingLists ul .DrawingList:active {
  transition-duration: 00ms;
  background: rgb(212, 212, 212);
  transform: scale(1.5);
}
.DrawingList a {
  display: block;
  color: inherit;
  text-decoration: none;
}

.DrawingForm .form {
  display: flex;
  padding: 0.5em 1em;
}
.DrawingForm .form input[type="text"] {
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.318);
  font-size: 1.5em;
  padding: 0.25em 1.5em;
  outline: none;
  font-weight: 600;
  border-radius: 10px;
  color: #282c34;
  margin: 0 1em;
}
.DrawingForm .form input[type="text"]:focus {
  box-shadow: inset 0 5px 5px rgba(0, 0, 0, 0.318);
  border-radius: 10px;
  /* text-shadow: 0 2px 5px rgba(0, 0, 0, 0.318); */
  border: 1px solid rgba(0, 0, 0, 0.13);
}

.DrawingForm .form input[type="submit"],
.NewAdd .save {
  border: 1px solid rgba(0, 0, 0, 0.318);
  font-size: 1.5em;
  padding: 0.25em 1.5em;
  outline: none;
  font-weight: 600;
  border-radius: 10px;
  color: #282c34;

  background: none;
}
.DrawingForm .form input[type="submit"]:hover,
.NewAdd .save:hover {
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.318);
}

.Drawing {
  height: calc(100% - 2em);
  margin: 1em 1em 1em 1em;
  /* padding-top: 1em;; */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.318);
  display: flex;
  flex-flow: column nowrap;
  background-color: #00dbde;
  background-image: linear-gradient(90deg, #00dbde 0%, #fc00ff 100%);
}

.NewAdd {
  display: flex;
  flex-flow: column nowrap;
  padding: 1em;
  background: rgba(0, 0, 0, 0.13);
}
.NewAdd * {
  font-size: 1em;
  margin-top: 0.5em;
  border: 1px solid rgba(0, 0, 0, 0.318);
  padding: 0.25em 0.5em;
  /* box-shadow: 0 2px 5px rgba(0, 0, 0, 0.318); */
  border-radius: 5px;
  transition-duration: 0.5s;
}
.NewAdd input {
  font-size: 1.5em;
}
.NewAdd input:focus {
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.318);
  border: none;
}
.NewAdd select {
  width: 100%;
  margin: 0.25em 1em;
}
.NewAdd .row {
  display: flex;
  flex-flow: row;
  justify-content: space-around;
  border: none;
  /* align-items: baseline; */
}
.NewAdd .save {
  margin-top: 0;
}

/* ColorBox colorList color */

.ColorBox {
  width: 100%;

  /* background: red; */
  padding: 0.25em 0;
}
.ColorBox .colorList {
  display: flex;
  flex-flow: row nowrap;
  padding: 0.25em;
}
.ColorBox .colorList .color {
  width: 1.5em;
  height: 1.5em;
  border-radius: 50%;
  margin: 0 0.25em;
  border: 2px solid white;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.253);
  cursor: pointer;
  transition-duration: 0.5s;
}

.ColorBox .colorList .color:active {
  box-shadow: inset 0 5px 5px rgba(0, 0, 0, 0.253);
}

.popup-content {
  background: transparent !important;
  border: none !important;
  padding: none !important;
  width: 70% !important;
}

.NewAdder {
  background: transparent;
  backdrop-filter: saturate(2) blur(5px);
  padding: 1em 3em;
}
.NewAdder input {
  width: 100%;
  font-size: 1.3em;
  border: 2px solid white;
  background: transparent;
  color: white;
  padding: 0.5em 1em;
  outline: none;
}
.NewAdder .btn {
  margin-top: 1em;
}
